      </div>
      <div class="slds-table--header-fixed_container configuration-list-content">
        <div class="slds-scrollable_y configuration-table-section">
          <table id="configuration-list" class="slds-table slds-table_bordered slds-table--header-fixed">
            <thead>
              <tr class="slds-line-height_reset">
