                  </span>
                </th>
                <th class="slds-cell-wrap" scope="col" style="width: 12%;">
                  <span class="slds-cell-fixed" style="width: 12%;">
                    <span class="slds-truncate" title="Assign Agent By">Assign Agent By</span>
                  </span>
                </th>
                <th class="slds-cell-wrap" scope="col" style="width: 15%;">
                  <span class="slds-cell-fixed" style="width: 15%;">
                    <span class="slds-truncate" title="Agent Unavailable Response">Agent Unavailable Response</span>
                  </span>
                </th>
                <th class="slds-cell-wrap" scope="col" style="width: 15%;">
                  <span class="slds-cell-fixed" style="width: 15%;">
                    <span class="slds-truncate" title="Automatically Accept Chats">Automatically Accept Chats</span>
                  </span>
                </th>
                <th class="slds-cell-wrap" scope="col" style="width: 12%;">
                  <span class="slds-cell-fixed" style="width: 12%;">
                    <span class="slds-truncate" title="Auto Transfer">Auto Transfer</span>
                  </span>
                </th>
                <th class="slds-cell-wrap" scope="col" style="width: 3rem;">
                </th>
              </tr>
            </thead>
            <tbody v-if="configurations.length > 0">
              <tr class="slds-hint-parent" v-for="(configuration, index) in configurations" :key="index">
                <td data-label="WECHAT ACCOUNT" class="first-header-label" scope="row">
                  <div class="slds-truncate" :title="configuration.WeChatAccountBaseInfo.Name">{{ configuration.WeChatAccountBaseInfo.Name }}</div>
                </td>
                <td data-label="START CHAT BY" scope="row">
                  <div class="slds-truncate" :title="configuration.StartChatBy">{{ configuration.StartChatBy }}</div>
                </td>
                <td data-label="ASSIGN AGENT BY" scope="row">
                  <div class="slds-truncate" :title="configuration.RoutingMode">{{ configuration.RoutingMode }}</div>
                </td>
                <td data-label="AGENT UNAVAILABLE RESPONSE">
                  <div class="slds-form-element">
                    <div class="slds-form-element__control">
                      <div class="slds-checkbox">
                        <input type="checkbox" name="options" id="checkbox1" disabled :checked="configuration.IsUnavailableAgentResponseEnabled"/>
                        <label class="slds-checkbox__label" for="checkbox1">
                          <span class="slds-checkbox_faux"></span>
                        </label>
                      </div>
                    </div>
                  </div>
