                <td data-label="AUTOMATICALLY ACCEPT CHATS">
                  <div class="slds-form-element">
                    <div class="slds-form-element__control">
                      <div class="slds-checkbox">
                        <input type="checkbox" name="options" id="checkbox2" disabled :checked="configuration.IsAutoAcceptChat"/>
                        <label class="slds-checkbox__label" for="checkbox2">
                          <span class="slds-checkbox_faux"></span>
                        </label>
                      </div>
                    </div>
                  </div>
                </td>
                <td data-label="AUTO TRANSFER">
                  <div class="slds-form-element">
                    <div class="slds-form-element__control">
                      <div class="slds-checkbox">
                        <input type="checkbox" name="options" id="checkbox3" disabled :checked="configuration.IsAutoTransferChat"/>
                        <label class="slds-checkbox__label" for="checkbox3">
                          <span class="slds-checkbox_faux"></span>
                        </label>
                      </div>
                    </div>
                  </div>
                </td>
                <td data-label="ACTION">
                  <action-menu :actions="configurationActions" :item="configuration" @edit="editConfiguration">
                  </action-menu>
                </td>
              </tr>
