      </div>
      <edit-configuration-modal :configuration="currentConfiguration" @close-edit-modal="closeEditModal" v-if="showEditModal"></edit-configuration-modal>
    </div>
  </right-page>
</template>

<script>

import EditConfigurationModal from '../components/live_chat_configurations/EditConfigurationModal.vue'
import { getLiveChatConfigurations } from '../api/charket-settings'

export default {
  name: 'LiveChatConfigurations',
  components: {
    EditConfigurationModal
  },
  data () {
    return {
      rightPageHeader: {
        label: 'CHARKET SETTINGS',
        name: 'Live Chat Configurations'
      },
      configurationActions: ['Edit'],
      configurations: [],
      showEditModal: false,
      currentConfiguration: {}
    }
  },
  created () {
    this.fetchLiveChatConfigurations()
  },
  methods: {
    async fetchLiveChatConfigurations () {
      try {
        this.$bus.emit('show-loading')
        this.configurations = await getLiveChatConfigurations()
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      } finally {
        this.$bus.emit('hide-loading')
      }
    },
    editConfiguration (item) {
      this.currentConfiguration = item
      this.showEditModal = true
    },
    closeEditModal (needToRefresh) {
      this.showEditModal = false
      if (needToRefresh) {
        this.fetchLiveChatConfigurations()
      }
    }
  }
}
</script>

