<template>
  <div>
    <section role="dialog" tabindex="-1" aria-labelledby="modal-heading-01" aria-modal="true" aria-describedby="modal-content-id-1" class="slds-modal slds-fade-in-open">
      <div class="slds-modal__container" :style="{transform: 'none', minWidth: '840px'}">
        <header class="slds-modal__header">
          <button class="slds-button slds-button_icon slds-modal__close slds-button_icon-inverse" title="Close" @click="closeEditModal()">
            <icon name="close" icon-class-name="slds-button__icon slds-button__icon_large">
            </icon>
            <span class="slds-assistive-text">Close</span>
          </button>
          <h2 class="slds-text-heading_medium slds-hyphenate">Edit Live Chat Configurations</h2>
        </header>
        <div class="slds-modal__content slds-p-around_medium" id="modal-content-id-1" v-if="!isLoading">
          <div class="slds-form-element form-section">
            <div class="slds-form-element__control">
              <div class="header-title">WeChat Account Information</div>
              <div class="section-content">
                <label class="slds-form-element__label label-info">WeChat Account</label>
                <div class="account-container">{{ currentConfiguration.WeChatAccountBaseInfo.Name }}</div>
              </div>
            </div>
          </div>
          <div class="slds-form-element form-section">
            <div class="slds-form-element__control">
              <div class="header-title">Start Chat Settings</div>
              <div class="section-content">
                <fieldset class="slds-form-element">
                  <legend class="slds-form-element__legend slds-form-element__label label-info">Start Chat By</legend>
                  <div class="slds-form-element__control">
                    <span class="slds-radio">
                      <input type="radio" id="radio-1" name="StartChatBy" value="Any Activity" v-model="currentConfiguration.StartChatBy" />
                      <label class="slds-radio__label" for="radio-1">
                        <span class="slds-radio_faux"></span>
                        <span class="slds-form-element__label">Any Activity</span>
                      </label>
                    </span>
                    <span class="slds-radio">
                      <input type="radio" id="radio-2" name="StartChatBy" value="Any Message" v-model="currentConfiguration.StartChatBy" />
                      <label class="slds-radio__label" for="radio-2">
                        <span class="slds-radio_faux"></span>
                        <span class="slds-form-element__label">Any Message</span>
                      </label>
                    </span>
                    <span class="slds-radio">
                      <input type="radio" id="radio-3" name="StartChatBy" value="Any Message except Smart Replies" v-model="currentConfiguration.StartChatBy" />
                      <label class="slds-radio__label" for="radio-3">
                        <span class="slds-radio_faux"></span>
                        <span class="slds-form-element__label">Any Message except Smart Replies</span>
                      </label>
                    </span>
                    <span class="slds-radio">
                      <input type="radio" id="radio-4" name="StartChatBy" value="Following Phrases" v-model="currentConfiguration.StartChatBy" />
                      <label class="slds-radio__label" for="radio-4">
                        <span class="slds-radio_faux"></span>
                        <span class="slds-form-element__label">Following Phrases (separate toggle input phrases with commas)</span>
                      </label>
                    </span>
                  </div>
                </fieldset>
                <div class="form-ele-section">
                  <textarea class="slds-textarea textarea-content" maxlength="255" :disabled="currentConfiguration.StartChatBy !== 'Following Phrases'" :value="startChatKeywordsValue" @input="setStartChatKeywordsValue($event)"></textarea>
                </div>
                <div class="slds-checkbox">
                  <input type="checkbox" name="options" id="auto-reply" :disabled="isMiniProgramAcc" v-model="currentConfiguration.IsInactiveChatResponseEnabled" />
                  <label class="slds-checkbox__label" for="auto-reply">
                    <span class="slds-checkbox_faux" :class="{'mini-program-disabled-checkbox': isMiniProgramAcc}"></span>
                    <span class="slds-form-element__label">When none of the above Start Chat by options is matched, auto reply the following message.</span>
                  </label>
                </div>
                <div class="response-container">
                  <wechat-response
                    v-model="startChatResponse"
                    :limit-of-items="3"
                    page-name="Live Chat Configurations"
                    :wechat-account="wechatAccount"
                    :choosers="['Text Message', 'Image', 'Structured Message', 'Voice Message', 'Video']"
                    :disabled="!currentConfiguration.IsInactiveChatResponseEnabled"
                    multiple
                    enable-reply-mode
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="slds-form-element form-section">
            <div class="slds-form-element__control">
              <div class="header-title">Assign Agent Settings</div>
              <div class="section-content">
                <fieldset class="slds-form-element">
                  <legend class="slds-form-element__legend slds-form-element__label label-info">Assign Agent By</legend>
                  <div class="slds-form-element__control">
                    <span class="slds-radio">
                      <input type="radio" id="radio-5" name="RoutingMode" value="Least Active" v-model="currentConfiguration.RoutingMode" />
                      <label class="slds-radio__label tooltip-label" for="radio-5">
                        <span class="slds-radio_faux"></span>
                        <span class="slds-form-element__label">Least Active</span>
                        <ld-tooltip>
                          <p>Least Active assigns to the agent with the fewest number of chatting chats.</p>
                          <icon
                            slot="activator"
                            name="info"
                            icon-class-name="slds-icon_xx-small info-icon"
                          ></icon>
                        </ld-tooltip>
                      </label>
                    </span>
                    <span class="slds-radio">
                      <input type="radio" id="radio-7" name="RoutingMode" value="Omni Channel" v-model="currentConfiguration.RoutingMode" />
                      <label class="slds-radio__label tooltip-label" for="radio-7">
                        <span class="slds-radio_faux"></span>
                        <span class="slds-form-element__label">Omni Channel</span>
                        <ld-tooltip>
                          <p>Use Omni-Channel to route chat requests to agents. Enabling Omni-Channel will disable certain Charket features including Live Chat, Live Chat Monitor and status based auto responses.</p>
                          <icon
                            slot="activator"
                            name="info"
                            icon-class-name="slds-icon_xx-small info-icon"
                          ></icon>
                        </ld-tooltip>
                      </label>
                    </span>
                    <span class="slds-radio">
                      <input type="radio" id="radio-6" name="RoutingMode" value="Custom" v-model="currentConfiguration.RoutingMode" />
                      <label class="slds-radio__label tooltip-label" for="radio-6">
                        <span class="slds-radio_faux"></span>
                        <span class="slds-form-element__label">Custom</span>
                        <ld-tooltip>
                          <p>An Apex class that implements the Charket.WeChatAgentSelector interface to be automatically executed by Charket to assign agent.</p>
                          <icon
                            slot="activator"
                            name="info"
                            icon-class-name="slds-icon_xx-small info-icon"
                          ></icon>
                        </ld-tooltip>
                      </label>
                    </span>
                  </div>
                </fieldset>
                <div class="form-ele-section">
                  <input class="slds-input" type="text" :disabled="currentConfiguration.RoutingMode !== 'Custom'" :value="customAgentSelectorValue" @input="setCustomAgentSelectorValue($event)" />
                </div>
                <fieldset class="slds-form-element">
                  <div class="slds-form-element__control">
                    <div class="slds-checkbox">
                      <input
                        type="checkbox"
                        name="OtherSettings"
                        id="checkbox-1"
                        :disabled="isOmniChannelRoutingMode"
                        v-model="currentConfiguration.IsAutoTransferChat"
                      />
                      <label class="slds-checkbox__label" for="checkbox-1">
                        <span
                          class="slds-checkbox_faux"
                          @mouseover="showTooltip($event, 'Omni Channel')"
                          @mouseout="hideTooltip"
                        ></span>
                        <span class="slds-form-element__label">Reroute WeChat followers after waiting for</span>
                        <input
                          class="slds-input min-input"
                          :class="{'slds-has-error': transferTimeRequired}"
                          min="1" type="number"
                          :disabled="!currentConfiguration.IsAutoTransferChat"
                          v-model="currentConfiguration.TransferChatWaitingTime"
                          @keydown="checkNum($event, currentConfiguration.TransferChatWaitingTime)" />
                        <span class="slds-form-element__label"> minutes.</span>
                      </label>
                    </div>
                    <div class="slds-checkbox">
                      <input
                        type="checkbox"
                        name="OtherSettings"
                        id="checkbox-2"
                        :disabled="isMiniProgramAcc || isOmniChannelRoutingMode"
                        v-model="currentConfiguration.IsUnavailableAgentResponseEnabled"
                      />
                      <label class="slds-checkbox__label" for="checkbox-2">
                        <span
                          class="slds-checkbox_faux"
                          :class="{'mini-program-disabled-checkbox': isMiniProgramAcc}"
                          @mouseover="showTooltip($event, 'Omni Channel')"
                          @mouseout="hideTooltip"
                        ></span>
                        <span class="slds-form-element__label">Reply the following message when there are no WeChat agents available for</span>
                        <input
                          class="slds-input min-input"
                          :class="{'readonly-style': currentConfiguration.IsUnavailableAgentResponseEnabled && currentConfiguration.RoutingMode === 'Custom', 'slds-has-error': unavailableTimeRequired}"
                          min="0"
                          type="number"
                          :disabled="!currentConfiguration.IsUnavailableAgentResponseEnabled || isMiniProgramAcc"
                          :readonly="currentConfiguration.IsUnavailableAgentResponseEnabled && currentConfiguration.RoutingMode === 'Custom'"
                          v-model="currentConfiguration.UnavailableAgentResponseTime"
                          @keydown="checkNum($event, currentConfiguration.UnavailableAgentResponseTime, true)"
                          @mouseover="showTooltip($event, 'Custom')"
                          @mouseout="hideTooltip"/>
                        <span class="slds-form-element__label"> minutes.</span>
                      </label>
                    </div>
                  </div>
                </fieldset>
                <div class="response-container">
                  <wechat-response
                    v-model="agentSettingsResponse"
                    :limit-of-items="3"
                    page-name="Live Chat Configurations"
                    :wechat-account="wechatAccount"
                    :choosers="['Text Message', 'Image', 'Structured Message', 'Voice Message', 'Video']"
                    :disabled="!currentConfiguration.IsUnavailableAgentResponseEnabled"
                    multiple
                    enable-reply-mode
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="slds-form-element form-section">
            <div class="slds-form-element__control">
              <div class="header-title">Other Settings</div>
              <div class="slds-grid section-content">
                <label class="slds-form-element__label" for="toggle">Automatically Accept Chats</label>
                <ld-tooltip v-if="isOmniChannelRoutingMode">
                  <p>This setting is not applicable for Omni Channel routing rules.</p>
                  <ld-checkbox-toggle
                    slot="activator"
                    v-model="currentConfiguration.IsAutoAcceptChat"
                    :disabled="isOmniChannelRoutingMode"
                  />
                </ld-tooltip>
                <ld-checkbox-toggle
                  v-if="!isOmniChannelRoutingMode"
                  v-model="currentConfiguration.IsAutoAcceptChat"
                />
              </div>
              <div
                class="slds-grid section-content post-message"
                v-if="!isMiniProgramAcc"
              >
                <ld-checkbox-toggle
                  label="Enable Post 48-hour Messages"
                  v-model="currentConfiguration.IsPost48HourMessagesEnabled"
                />
                <ld-tooltip>
                  <p>Text messages sent after the 48-hour window are converted to WeChat articles before they are delivered to WeChat users. Such WeChat articles might be unintentionally shared with more WeChat users.</p>
                  <icon
                    slot="activator"
                    name="info"
                    icon-class-name="slds-icon_xx-small info-icon"
                  ></icon>
                </ld-tooltip>
              </div>
              <div class="slds-grid section-content">
                <ld-checkbox-toggle
                  label="Enable Smart Replies during Active Chat Session"
                  v-model="currentConfiguration.IsSmartRepliesEnabled"
                />
              </div>
            </div>
          </div>

          <div
            :style="{left: infoTooltipPosition.left, top: infoTooltipPosition.top}"
            class="slds-popover slds-popover_tooltip slds-nubbin_bottom-left tooltip"
            role="tooltip"
            v-if="showInfo"
          >
            <div class="slds-popover__body">{{ tooltipContent }}</div>
          </div>
        </div>
        <loading :is-loading="isLoading" custom-style="position: relative; padding : 40px; background-color: white"></loading>
        <footer class="slds-modal__footer">
          <button class="slds-button slds-button_neutral" @click="closeEditModal">Cancel</button>
          <button class="slds-button slds-button_brand" :disabled="isSaving" @click="saveConfiguration">Save</button>
        </footer>
      </div>
    </section>
    <div class="slds-backdrop slds-backdrop_open"></div>
  </div>
</template>

<script>
import WechatResponse from '../../components/wechat_response'
import { MessageService } from '../../service'
import { LdCheckboxToggle } from 'charket-ui/dist/ld-checkbox-toggle.js'
import { LdTooltip } from 'charket-ui/dist/ld-tooltip.js'
import { getLiveChatConfiguration, saveLiveChatConfiguration } from '../../api/charket-settings.js'
import CheckLimitNum from '../../mixins/checkLimitNum.js'

export default {
  name: 'EditConfigurationModal',
  mixins: [CheckLimitNum],
  components: {
    WechatResponse,
    LdCheckboxToggle,
    LdTooltip
  },
  props: {
    configuration: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  data () {
    return {
      currentConfiguration: {
        WeChatAccountBaseInfo: {
          Name: null,
          Id: null
        },
        StartChatBy: null,
        StartChatKeywords: null,
        RoutingMode: null,
        IsInactiveChatResponseEnabled: false,
        InactiveChatResponse: null,
        CustomAgentSelector: null,
        IsAutoAcceptChat: false,
        IsAutoTransferChat: false,
        TransferChatWaitingTime: null,
        IsUnavailableAgentResponseEnabled: false,
        UnavailableAgentResponseTime: null,
        UnavailableAgentResponse: null,
        IsPost48HourMessagesEnabled: false,
        IsSmartRepliesEnabled: true
      },
      isSaving: false,
      isLoading: false,
      startChatResponseScratch: [],
      agentSettingsResponseScratch: [],
      time: {
        TransferChatWaitingTime: null,
        UnavailableAgentResponseTime: null
      },
      showInfo: false,
      infoTooltipPosition: {
        left: '0px',
        top: '0px'
      },
      unavailableTimeRequired: false,
      transferTimeRequired: false,
      assignAgentSettings: {
        IsAutoTransferChat: false,
        IsUnavailableAgentResponseEnabled: false
      },
      tooltipContent: '',
      startChatResponse: {
        mode: 'Random',
        items: []
      },
      agentSettingsResponse: {
        mode: 'Random',
        items: []
      }
    }
  },
  computed: {
    wechatAccount () {
      return {
        id: this.currentConfiguration.WeChatAccountBaseInfo.Id,
        name: this.currentConfiguration.WeChatAccountBaseInfo.Name,
        type: this.currentConfiguration.WeChatAccountBaseInfo.Type
      }
    },
    startChatKeywordsValue () {
      if (this.currentConfiguration.StartChatBy !== 'Following Phrases') {
        return null
      }
      return this.currentConfiguration.StartChatKeywords
    },
    customAgentSelectorValue () {
      if (this.currentConfiguration.RoutingMode !== 'Custom') {
        return null
      }
      return this.currentConfiguration.CustomAgentSelector
    },
    isMiniProgramAcc () {
      return this.currentConfiguration.WeChatAccountBaseInfo.Type === 'Mini Program'
    },
    isOmniChannelRoutingMode () {
      return this.currentConfiguration.RoutingMode === 'Omni Channel'
    }
  },
  watch: {
    'currentConfiguration.RoutingMode' (val) {
      if (val === 'Custom') {
        this.tooltipContent = 'This setting is not applicable for customized routing rules.'
      } else if (val === 'Omni Channel') {
        this.tooltipContent = 'This setting is not applicable for Omni Channel routing rules.'
      }
      if (!this.currentConfiguration.IsUnavailableAgentResponseEnabled || val === 'Omni Channel') {
        return null
      }
      if (val === 'Custom') {
        this.time.UnavailableAgentResponseTime = this.currentConfiguration.UnavailableAgentResponseTime
        this.currentConfiguration.UnavailableAgentResponseTime = 0
      } else {
        this.currentConfiguration.UnavailableAgentResponseTime = this.time.UnavailableAgentResponseTime === 0 ? 0 : this.time.UnavailableAgentResponseTime || null
      }
    },
    'currentConfiguration.IsInactiveChatResponseEnabled' (value) {
      if (!value) {
        this.startChatResponseScratch = this.startChatResponse.items
        this.startChatResponse.items = []
      } else {
        this.startChatResponse.items = this.startChatResponseScratch
      }
      this.currentConfiguration.InactiveChatResponse.MessageIds = this.startChatResponse.items.map((item) => {
        return item.id
      })
    },
    'currentConfiguration.IsUnavailableAgentResponseEnabled' (value) {
      if (!value) {
        this.agentSettingsResponseScratch = this.agentSettingsResponse.items
        this.agentSettingsResponse.items = []
        this.time.UnavailableAgentResponseTime = this.currentConfiguration.UnavailableAgentResponseTime
        this.currentConfiguration.UnavailableAgentResponseTime = null
      } else {
        this.agentSettingsResponse.items = this.agentSettingsResponseScratch
        if (this.currentConfiguration.RoutingMode === 'Custom') {
          this.currentConfiguration.UnavailableAgentResponseTime = 0
        } else {
          this.currentConfiguration.UnavailableAgentResponseTime = this.time.UnavailableAgentResponseTime === 0 ? 0 : this.time.UnavailableAgentResponseTime || null
        }
      }
      this.currentConfiguration.UnavailableAgentResponse.MessageIds = this.agentSettingsResponse.items.map((item) => {
        return item.id
      })
    },
    'currentConfiguration.IsAutoTransferChat' (val) {
      if (!val) {
        this.time.TransferChatWaitingTime = this.currentConfiguration.TransferChatWaitingTime
        this.currentConfiguration.TransferChatWaitingTime = null
      } else {
        this.currentConfiguration.TransferChatWaitingTime = this.time.TransferChatWaitingTime
      }
    },
    'currentConfiguration.UnavailableAgentResponseTime' (val) {
      if (val) {
        this.unavailableTimeRequired = false
      }
    },
    'currentConfiguration.TransferChatWaitingTime' (val) {
      if (val) {
        this.transferTimeRequired = false
      }
    },
    startChatResponse: {
      handler (value) {
        this.currentConfiguration.InactiveChatResponse.MessageIds = value.items.map((item) => {
          return item.id
        })
      },
      deep: true
    },
    agentSettingsResponse: {
      handler (value) {
        this.currentConfiguration.UnavailableAgentResponse.MessageIds = value.items.map((item) => {
          return item.id
        })
      },
      deep: true
    },
    isOmniChannelRoutingMode (val) {
      if (val) {
        this.assignAgentSettings = {
          IsAutoTransferChat: this.currentConfiguration.IsAutoTransferChat,
          IsUnavailableAgentResponseEnabled: this.currentConfiguration.IsUnavailableAgentResponseEnabled
        }
        this.currentConfiguration.IsAutoTransferChat = false
        this.currentConfiguration.IsUnavailableAgentResponseEnabled = false
      } else {
        this.currentConfiguration.IsAutoTransferChat = this.assignAgentSettings.IsAutoTransferChat ||
          this.currentConfiguration.IsAutoTransferChat
        this.currentConfiguration.IsUnavailableAgentResponseEnabled = this.assignAgentSettings.IsUnavailableAgentResponseEnabled ||
          this.currentConfiguration.IsUnavailableAgentResponseEnabled
      }
    }
  },
  async created () {
    this.isLoading = true
    try {
      this.currentConfiguration = await getLiveChatConfiguration(this.configuration.WeChatAccountBaseInfo.Id)
      this.time = {
        TransferChatWaitingTime: this.currentConfiguration.TransferChatWaitingTime,
        UnavailableAgentResponseTime: this.currentConfiguration.UnavailableAgentResponseTime
      }
      const setResponse = (response, type) => {
        if (!response) {
          if (this.isMiniProgramAcc) {
            this.currentConfiguration[type] = null
            return
          }
          this.currentConfiguration[type] = {
            ReplyMode: 'Random',
            MessageIds: [],
            Name: type === 'InactiveChatResponse' ? 'Response for Start Chat' : 'Response for Agent Unavailable',
            UsedFor: type === 'InactiveChatResponse' ? 'Start Chat Reply' : 'Agent Unavailable Reply',
            Id: null
          }
        }
      }
      const inactiveResponse = this.currentConfiguration.InactiveChatResponse
      const unavailableAgentResponse = this.currentConfiguration.UnavailableAgentResponse
      setResponse(inactiveResponse, 'InactiveChatResponse')
      setResponse(unavailableAgentResponse, 'UnavailableAgentResponse')
      const inactiveResponseHasMsg = inactiveResponse && inactiveResponse.MessageIds.length > 0
      const unavailableAgentResponseHasMsg = unavailableAgentResponse && unavailableAgentResponse.MessageIds.length > 0
      if (inactiveResponseHasMsg && unavailableAgentResponseHasMsg) {
        await Promise.all([
          this.getStartChatResponseMessages(inactiveResponse.MessageIds),
          this.getAgentSettingsResponseMessages(unavailableAgentResponse.MessageIds)
        ])
      } else if (!inactiveResponseHasMsg && unavailableAgentResponseHasMsg) {
        await this.getAgentSettingsResponseMessages(unavailableAgentResponse.MessageIds)
      } else if (inactiveResponseHasMsg && !unavailableAgentResponseHasMsg) {
        await this.getStartChatResponseMessages(inactiveResponse.MessageIds)
      }
    } catch (error) {
      this.$bus.emit('error', { content: error.msg, type: 'error' })
    } finally {
      this.isLoading = false
    }
  },
  methods: {
    setStartChatKeywordsValue (event) {
      this.currentConfiguration.StartChatKeywords = event.target.value
    },
    setCustomAgentSelectorValue (event) {
      this.currentConfiguration.CustomAgentSelector = event.target.value
    },
    async getStartChatResponseMessages (messageIds) {
      await this.$try(async () => {
        const data = await MessageService.getMessagesByIds(messageIds)
        if (this.currentConfiguration.IsInactiveChatResponseEnabled) {
          this.startChatResponse.items = data
        } else {
          this.startChatResponseScratch = data
        }
      })
    },
    async getAgentSettingsResponseMessages (messageIds) {
      await this.$try(async () => {
        const data = await MessageService.getMessagesByIds(messageIds)
        if (this.currentConfiguration.IsUnavailableAgentResponseEnabled) {
          this.agentSettingsResponse.items = data
        } else {
          this.agentSettingsResponseScratch = data
        }
      })
    },
    closeEditModal () {
      this.$emit('close-edit-modal', false)
    },
    async saveConfiguration () {
      this.unavailableTimeRequired = this.currentConfiguration.IsUnavailableAgentResponseEnabled && !this.currentConfiguration.UnavailableAgentResponseTime && this.currentConfiguration.UnavailableAgentResponseTime !== 0 && this.currentConfiguration.RoutingMode === 'Least Active'
      this.transferTimeRequired = this.currentConfiguration.IsAutoTransferChat && !this.currentConfiguration.TransferChatWaitingTime
      if (this.unavailableTimeRequired || this.transferTimeRequired) {
        return null
      }
      if (!this.currentConfiguration.IsInactiveChatResponseEnabled) {
        if (this.currentConfiguration.InactiveChatResponse && this.currentConfiguration.InactiveChatResponse.Id) {
          this.currentConfiguration.InactiveChatResponse = {
            Id: this.currentConfiguration.InactiveChatResponse.Id
          }
        } else {
          this.currentConfiguration.InactiveChatResponse = null
        }
      }
      if (!this.currentConfiguration.IsUnavailableAgentResponseEnabled) {
        if (this.currentConfiguration.UnavailableAgentResponse && this.currentConfiguration.UnavailableAgentResponse.Id) {
          this.currentConfiguration.UnavailableAgentResponse = {
            Id: this.currentConfiguration.UnavailableAgentResponse.Id
          }
        } else {
          this.currentConfiguration.UnavailableAgentResponse = null
        }
      }
      this.currentConfiguration.TransferChatWaitingTime = this.currentConfiguration.TransferChatWaitingTime || null
      if (this.currentConfiguration.UnavailableAgentResponseTime !== 0) {
        this.currentConfiguration.UnavailableAgentResponseTime = this.currentConfiguration.UnavailableAgentResponseTime || null
      }
      this.currentConfiguration.StartChatKeywords = this.currentConfiguration.StartChatBy === 'Following Phrases' ? this.currentConfiguration.StartChatKeywords.replace(//g, ',') : null
      this.currentConfiguration.CustomAgentSelector = this.currentConfiguration.RoutingMode === 'Custom' ? this.currentConfiguration.CustomAgentSelector : null
      try {
        this.isSaving = true
        await saveLiveChatConfiguration(JSON.stringify(this.currentConfiguration))
        this.$bus.emit('success', { content: 'Live Chat Configuration was saved.', type: 'success' })
        this.$emit('close-edit-modal', true)
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      } finally {
        this.isSaving = false
      }
    },
    showTooltip (event, routingMode) {
      if (this.currentConfiguration.RoutingMode !== routingMode) {
        return null
      }

      this.showInfo = true
      this.$nextTick(() => {
        let infoIconPosition = event.target.getBoundingClientRect()
        let tooltipEle = document.querySelector('.tooltip')
        // 16 is the tooltip left border
        // 12 is the arrow height
        this.infoTooltipPosition.left = `${infoIconPosition.left - 16}px`
        this.infoTooltipPosition.top = `${infoIconPosition.top - tooltipEle.clientHeight - 12}px`
      })
    },
    hideTooltip () {
      this.infoTooltipPosition = {
        left: '0px',
        top: '0px'
      }
      this.showInfo = false
    }
  }
}
</script>

<style lang="scss" scoped>
  .slds-modal__content {
    padding: 32px;
    .tooltip {
      position: fixed;
    }
  }
  .form-section {
    margin-bottom: 20px;
    color: #3E3E3C;
    .header-title {
      height: 32px;
      background: #F3F2F2;
      border-radius: 4px;
      border: 1px solid #F3F2F2;
